@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: normal;
  src: local("HelveticaRegular"),
    url("./HelveticaRegular.woff2") format("woff2");
}

@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 700;
  src: local("HelveticaBold"), url("./HelveticaBold.woff2") format("woff2");
}

@font-face {
  font-family: "Yeseva One";
  font-style: normal;
  font-weight: normal;
  src: local("YesevaOne-Regular"),
    url("./YesevaOne-Regular.woff2") format("woff2");
}
